import React, { useEffect, useState } from 'react';
import './App.css';
import Recipe from './Recipe';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
const element = <FontAwesomeIcon icon={faSearch} />
const App = () => {
  const APP_ID = '6d4dc5a1';
  const APP_KEY = '267cf85ffc377067fbc643ac61465f13';
  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState('');
  const [query, setQuery] = useState('chicken');

  useEffect(() => {

    getRecipes();
  }, [query]);

  const getRecipes = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}&from=0&to=9&calories=591-722&health=alcohol-free`);
    const data = await response.json();
    setRecipes(data.hits);
  }
  const updateSearch = e => {
    setSearch(e.target.value);
  }

  const getSearch = e => {
    e.preventDefault();
    setQuery(search);
    setSearch('');
  }
  return (
    <div className="App">
      <form className="search-form" onSubmit={getSearch}>
        <input className="search-bar" type="text" value={search} onChange={updateSearch} />
        <button className="search-btn" type="submit">{element}</button>
      </form>
      <div className="content">
        {
          recipes.map(recipe => (
            <Recipe
              key={recipe.recipe.label}
              title={recipe.recipe.label}
              calories={recipe.recipe.calories}
              image={recipe.recipe.image}
              item={recipe.recipe.ingredients} />
          ))
        }</div>
    </div>
  );
}

export default App;
