import React from 'react'
import style from './recipe.module.css';
export default function Recipe({ title, calories, image, item }) {
    return (
        <div className={style.recipe}>
            <h1>{title}</h1>
            <p>Calories:{calories.toFixed(2)}</p>


            <img src={image} alt="demo" />
        </div>
    );
}
